.section-title {
    font-size: 44px;
    margin-bottom: 20px;
    font-weight: 600;
    padding-right: 300px;
}

.step_content--wrp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 450px;


    .ver2-0 {
        text-align: center;
        font-size: 44px;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-bottom: 90px;
    }

}

.inputs-wrp {
    margin-left: 120px;
    flex-basis: 55%;
}

.input-group3 {

    margin-bottom: 30px;

}

.input-group3__label {
    font-size: 44px;
    margin-bottom: 15px;
    display: block;
    padding-right: 50px;
}

.input-group3__input {
    width: 441px;
    height: 42px;
    border: 4px solid $black;
    font-size: 18px;
    font-weight: 600;
    padding-left: 20px;

}

.counts-wrp {
    flex-basis: 45%;
    padding: 15px;
    border: 4px solid $black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 40px;



    &__txt {
        font-size: 42px;
        font-weight: 600;
        text-align: center;
        line-height: 55px;
    }

    &__txt--red {
        color: $red;
    }

    &__benifit {
        background: $red;
        padding: 10px 12px;
        width: 100%;
        color: #fff;
        font-size: 42px;
        font-weight: 600;
        text-align: center;
        margin-top: 18px;
        line-height: 55px;

    }

}



@media only screen and (max-width : 1564px) {


    .inputs-wrp {
        margin-left: 0;
        flex-basis: 50%;
    }


    .input-group3__label {
        font-size: 32px;

    }


    .counts-wrp {
        margin-right: 0;



        &__txt {
            font-size: 32px;
            line-height: 55px;
        }


        &__benifit {
            font-size: 32px;
            line-height: 55px;

        }

    }



}

@media only screen and (max-width : 1200px) {
    .input-group3__label {

        padding-right: 0;
    }

    .section-title {

        padding-right: 0px;
    }

    .step_content--wrp {
        flex-wrap: wrap;

    }

    .inputs-wrp {
        margin-left: 0;
        flex-basis: 100%;
        margin-bottom: 20px;
    }

    .counts-wrp {
        flex-basis: 100%;
        margin-right: 0;


    }
}

@media only screen and (max-width : 576px) {


    .counts-wrp {
        margin-top: 20px;
    }

    .step_content--calc {

        padding-left: 23px;
        padding-right: 23px;
    }

    .section-title {
        font-size: 20px;

        span {
            font-size: 14px;
        }
    }

    .inputs-wrp {
        margin-bottom: 0;
    }

    .input-group3__label {
        font-size: 20px;
        line-height: 26px;
        margin-bottom: 5px;
    }

    .input-group3__input {
        width: 80%;
        height: 20px;
        border: 2px solid $black;
        font-size: 14px;
        margin-left: 20%;

    }

    .input-group3 {
        margin-bottom: 12px;

    }

    .counts-wrp {
        padding: 11px;
        border: 2px solid $black;

        &__txt {
            font-size: 20px;
            line-height: 22px;
            margin-bottom: 10px;
        }


        &__benifit {
            margin-top: 0px;
            font-size: 20px;
            line-height: 26px;

        }

    }

    .step_content--wrp {
        height: 100%;

        .ver2-0 {

            font-size: 22px;

        }

    }


}
