.counts-wrapper {
    display: flex;
    flex-direction: column;
    flex-basis: 50%;
    margin-left: 10px;
}

.f-align-start {
    align-items: flex-start;
}

@media only screen and (max-width : 1200px) {
    .counts-wrapper {
        flex-basis: 100%;
        margin-left: 0;
    }


}
