.input-flex {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    &:last-child {
        margin-bottom: 0;
    }

    .input-group3__label {
        margin-bottom: 0;
        margin-right: 15px;

    }
}

.inputs-wrp--title {
    font-size: 44px;
    margin-bottom: 15px;
}

.input-offset {
    margin-bottom: 0;

    input {
        margin-left: 25%;
        width: 66%;
    }
}

.step-dark {
    background: #CCCCCC;
}

@media only screen and (max-width : 1564px) {
    .input-offset {
        input {
            margin-left: 0;
            width: 87%;
        }
    }
}

@media only screen and (max-width : 576px) {
    .inputs-wrp--title {
        margin-bottom: 10px;
        font-size: 20px;
    }

    .input-flex {
        margin-bottom: 7px;

        input {
            width: 100%;
            margin-left: 0px;
        }
    }

    .input-offset {
        margin-bottom: 54px;

        input {
            width: 80%;
            margin-left: 20%;
        }
    }
}
