.content-tabs {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.tabs-btns {
    display: flex;
    width: 102.5%;
    margin-top: -15px;
    margin-left: -14px;

    .btn-active {
        background: black;
    }
}

.tab-btn {
    font-size: 29px;
    font-weight: 600;
    color: #fff;
    background: #AFAEAE;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    cursor: pointer;
    border-right: 2px solid #fff;
    line-height: 37.41px;
    padding: 15px 5px;
    flex: 1 1 auto;

    &:hover {
        background: $black;
    }


    &:last-child {
        border-right: none;
    }

}


.tabs-info .tab-info {
    display: none;
}

.tabs-info .tab-active {
    display: block;
}

.tab-blocks-wrp {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 15px;
}

.tab-block {
    flex-basis: 32.8%;
    border: 4px solid #201600;
    padding: 26px 5px 6px 25px;

    margin-bottom: 6px;
    margin-right: 6px;
    position: relative;
    height: 193px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.tab-block-padding {
    padding: 40px 15px 15px 15px;
}

.tab-block-title {
    font-weight: 600;
    font-size: 20px;
    color: #000105;
    display: block;
    position: absolute;
    left: 5px;
    top: 5px;
}

.tab-block-info {
    display: flex;
    flex-direction: column;
}

.tab-block-info-row {
    display: flex;
    align-items: center;
}

.tab-block-info {
    font-weight: 400;
    font-size: 20px;
    line-height: 21px;
    text-align: right;
    color: #000105;

}

.col-black {
    flex-basis: 60%;
    margin-right: 15px;
    font-size: 20px;
}

.col-red {
    flex-basis: 40%;
    font-weight: 600;
    font-size: 42px;
    text-align: left;
    color: #BA205A;
    line-height: 48px;
}

.buttons-tabs {
    display: flex;
    justify-content: center;

    .radio-tile {
        flex-direction: row !important;
        align-items: center !important;

        .icon {
            margin-bottom: 0;
            margin-right: -40px;
            margin-bottom: 18px;


        }
    }

    .radio-tile-group {
        flex-basis: 35%;
        margin-top: 30px;
        margin-right: 40px;

        &:last-child {
            margin-right: 0;
        }
    }

    .radio-tile-label-five {

        padding: 5px 30px;
        font-size: 28px;
        margin-top: 0;
        line-height: 1;
    }

    .icon-envelop {
        margin-bottom: 35px !important;
    }

}

.text-centered {

    text-align: center;
    margin-right: 0;
    left: 5px;
    right: 5px;

}

.text-bold {
    font-weight: 600;
}

.text-low-case {
    text-transform: none;
}

.text-fonds {
    font-weight: 400;
    font-size: 19.58px;
    line-height: 25px;
    text-align: center;
    color: #000105;


}

.red-text {
    font-weight: 600;
    font-size: 42px;
    text-align: left;
    color: #BA205A;
    line-height: 48px;

}

.text-fonds-pt {
    padding-top: 30px;
}

.step_content--tabs {
    display: flex !important;
    flex-direction: column;
    justify-content: space-between;
}

@media only screen and (max-width : 1564px) {
    .tab-block {
        flex-basis: 49%;
    }
}

@media only screen and (max-width:1372px) {
    .tab-btn {
        font-size: 22px;
    }

    .tabs-btns {
        width: 103%;
    }
}

@media only screen and (max-width : 810px) {
    .tab-block {
        flex-basis: 100%;
        margin-right: 0;
    }

}

@media only screen and (max-width:768px) {
    .tabs-btns {
        flex-wrap: wrap;
        width: 107%;

    }

    .tab-btn {
        flex-basis: 100%;
        margin-bottom: 1px;
        font-size: 16px;
        padding: 7px 0;
        line-height: 16px;
    }
}

@media only screen and (max-width : 576px) {
    .tab-block-title {
        font-size: 15px;
    }

    .col-black {
        font-size: 15px;
        line-height: 14px;
    }

    .col-red {
        font-size: 32px;
    }

    .buttons-tabs {
        display: flex;
        justify-content: center;

        .radio-tile {
            flex-direction: column !important;
            align-items: center !important;

            .icon {
                margin-bottom: 0;
                margin-right: 0px;
                margin-bottom: -10px;


            }
        }

        .radio-tile-group {
            flex-basis: 50%;
            margin-top: 30px;
            margin-right: 0;
        }

        .radio-tile-label-five {

            padding: 5px 20px;
            font-size: 28px;
            margin-top: 0;
            line-height: 1;
            display: flex;
            align-items: center;
        }

        .icon-envelop {
            margin-bottom: -10px !important;
        }

    }

    .text-fonds {
        font-weight: 400;
        font-size: 15px;
        line-height: 15px;



    }

    .red-text {

        font-size: 32px;

    }

    .tab-block-title {

        text-align: left;
    }
}
